.reset_password {
  border-radius: 10px;
  padding: 2.5rem 1rem;
}

.reset_password .reset_password_title {
  text-align: center;
}

.reset_password .reset_password_title h2 {
  color: var(--text-color);
  font-size: 25px;
}

.reset_password .form_container {
  margin-top: 2rem;
  padding: 20px;
  width: 340px;
  background-color: rgba(230, 243, 243, 0.4);
  z-index: 90;
  margin-bottom: 1rem;
}

.reset_button button {
  padding: 5px 50px;
}

form .error {
  margin: auto;
}

@media (max-width: 480px) {
  .reset_password .form_container {
    width: 300px;
  }
}
@media (max-width: 375px) {
  .reset_password .form_container {
    width: 260px;
  }
}
@media (max-width: 330px) {
  .reset_password .form_container {
    width: 230px;
  }
}
